<template>
  <div id="home">
    <head-tabar></head-tabar>
    <el-container style="margin-top: 80px;height: 100%">
      <el-aside style="width: 250px;margin-left: 10px;border-radius: 5px">
        <div class="user">
          <el-avatar :src="require(`@/assets/${headImg}`)"
                     style="width: 150px;height: 150px;margin-left: 50px;margin-top: 20px"></el-avatar>
          <p style="font-size: 25px;margin-top: 5px;text-align: center;margin-bottom: 0px">{{ name }}</p>
        </div>
        <div>
          <el-menu
            class="el-menu-vertical-demo"
            router>
            <el-menu-item index="/Home/Course">
              <i class="el-icon-menu"></i>
              <span slot="title">课程</span>
            </el-menu-item>
            <el-menu-item index="2">
              <i class="el-icon-setting"></i>
              <span slot="title">专题创作</span>
            </el-menu-item>
            <el-menu-item index="3">
              <i class="el-icon-chat-dot-square"></i>
              <span slot="title">收件箱</span>
            </el-menu-item>
            <el-menu-item index="4">
              <i class="el-icon-s-custom"></i>
              <span slot="title">小组</span>
            </el-menu-item>
            <el-menu-item index="5">
              <i class="el-icon-edit"></i>
              <span slot="title">笔记</span>
            </el-menu-item>
            <el-menu-item index="6">
              <i class="el-icon-cloudy"></i>
              <span slot="title">云盘</span>
            </el-menu-item>
            <el-menu-item index="7">
              <i class="el-icon-collection-tag"></i>
              <span slot="title">通讯录</span>
            </el-menu-item>
          </el-menu>
        </div>
      </el-aside>
      <el-main style="padding: 0px;margin-left: 20px;margin-right: 20px">
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import HeadTabar from "@/components/HomeTaber/HeadTabar.vue";
export default {
  name: "Home",
  data() {
    return {
      headImg: localStorage.getItem('userHead'),
      name: localStorage.getItem("userName"),
      imgUrl: require("@/assets/qrcode.jpg")
    }
  },
  components: {
    HeadTabar
  }
}
</script>

<style scoped>
#home {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #d6d6d6;
}

.user {
  background-image: url("../../assets/backgroundimg.jpeg");
  background-size: 100% 100%;
}
</style>



